<template>
    <div>
<!--        <vs-divider>-->
<!--            Настройки группы-->
<!--        </vs-divider>-->

<!--        <vs-input-number min="1" max="12" v-model="columnCount"/>-->

        <div>
            <vs-divider>
                Список маяков
            </vs-divider>

            <vs-row v-if="currentSection !== null">
                <vs-col type="flex" vs-justify="center" vs-align="center" vs-sm="12" :vs-lg="columnCount" v-for="module in currentSection.modules">
                    <vs-card actionable @contextmenu.prevent.native="$refs.menu.open($event, module)">
                        <div class="card-content-section">
                            <h5 class="mb-1">{{ module.alias }}</h5>
                            ID: {{ module.did }}
                        </div>
                    </vs-card>
                </vs-col>

                <vs-col type="flex" vs-justify="center" vs-align="center" vs-sm="12" :vs-lg="columnCount">
                    <vs-card actionable @click.native="addModule">
                        <div class="card-content-section" >
                            <h5 style="color: #7367f0">+ Добавить маяк</h5>
                        </div>
                    </vs-card>
                </vs-col>
            </vs-row>
        </div>

        <vs-prompt
                vs-title="Новый модуль"
                vs-accept-text="Добавить"
                vs-cancel-text="Отменить"
                @vs-cancel="cancelAddModule"
                @vs-accept="saveModule"
                @vs-close="cancelAddModule"
                :vs-active.sync="dialogAddModule">
            <div class="con-exemple-prompt">
                <div class="mb-3">
                    <span>Введите наименование</span>
                    <vs-input placeholder="Ячейка 1" vs-placeholder="Ячейка 1" v-model="newModuleAlias" class="mt-3 w-full" />
                </div>

                <div>
                    <span>Введите ID модуля</span>
                    <vs-input placeholder="201905040001" vs-placeholder="201905040001" v-model="newModuleId" class="mt-3 w-full" />
                </div>
            </div>
        </vs-prompt>

        <vs-prompt
                vs-title="Редакитровать маяк"
                vs-accept-text="Сохранить"
                vs-cancel-text="Отменить"
                @vs-cancel="cancelAddModule"
                @vs-accept="saveUpdateModule"
                @vs-close="cancelAddModule"
                :vs-active.sync="dialogUpdateModule">
            <div class="con-exemple-prompt">
                <div class="mb-3">
                    <span>Введите наименование</span>
                    <vs-input placeholder="Ячейка 1" vs-placeholder="Ячейка 1" v-model="newModuleAlias" class="mt-3 w-full" />
                </div>

                <div>
                    <span>Введите ID модуля</span>
                    <vs-input placeholder="201905040001" vs-placeholder="201905040001" v-model="newModuleId" class="mt-3 w-full" />
                </div>
            </div>
        </vs-prompt>

        <vue-context ref="menu">
            <template slot-scope="child">
                <ul class="bordered-items p-0">
                    <li @click="optionClicked($event.target.innerText, child.data)">Редактировать</li>
                    <li @click="optionClicked($event.target.innerText, child.data)">Удалить</li>
                </ul>
            </template>
        </vue-context>

    </div>
</template>

<script>
    import { VueContext } from 'vue-context';

    export default {
        components: { VueContext },
        data(){
            return {
                currentSection: null,

                columnCount: 3,
                dialogAddModule: false,
                newModuleAlias: '',
                newModuleId: '',

                newModuleTitle: '',
                dialogUpdateModule: false,

                lastModuleId: null
            }
        },
        created() {
            this.$vs.loading();

            this.$store.dispatch('modules/getSectionById', this.$route.params.section).then((data) => {
                this.$emit('changeRouteTitle', data.title);
                this.currentSection = data;
                this.$vs.loading.close();
            }).catch((e) => {
                this.$vs.loading.close();
                this.$router.push('/modules');
            });


        },
        methods: {
            optionClicked(text, data) {
                if(text === 'Редактировать'){
                    this.editModule(data);
                }else{
                    this.lastModuleId = data;
                    this.$vs.dialog({
                        type: 'confirm',
                        color: 'danger',
                        title: `Подтвердите удаление`,
                        text: 'Удалить?',
                        acceptText: 'Удалить',
                        cancelText: 'Отменить',
                        accept: this.deleteModule
                    })
                }
            },
            async deleteModule(){
                try{
                    await this.$http.delete('/modules/' + this.lastModuleId.id);
                    this.$store.commit('modules/moduleDelete', this.lastModuleId );
                }catch (e) {
                    console.log(e)
                }
            },
            addModule(){
                this.dialogAddModule = true;
            },
            cancelAddModule(){
                this.dialogAddModule = false;
                this.newModuleAlias = '';
                this.newModuleId = '';
                this.dialogUpdateModule = false;
            },
            saveModule(){
                this.$http.post('/modules', {
                    name: this.newModuleAlias,
                    pid: this.$route.params.section,
                    did: this.newModuleId
                }).then((data) => {
                    this.$vs.notify({title:'Успешно!',text:'Модуль добавлен.',color:'success'});
                }).catch((e) => {
                    this.$vs.notify({title: 'Ошибка!', text: e.response.data.err, color: 'red'});
                });


                this.cancelAddModule();
            },
            editModule(data){
                console.log(data);
                this.lastModuleId = data.id;
                this.newModuleAlias = data.alias;
                this.newModuleId = data.did;
                this.dialogUpdateModule = true;
            },
            saveUpdateModule(){
                this.$http.post('/modules/' + this.lastModuleId, {
                    alias: this.newModuleAlias,
                    did: this.newModuleId,
                }).then((res) => {
                    this.$store.commit('modules/updateModule', res.data);
                    this.$vs.notify({title:'Успешно!',text:'Маяк сохранен.',color:'success'});
                }).catch((e) => {
                    console.log(e)
                    this.$vs.notify({title: 'Ошибка!', text: e.response.data.err, color: 'red'});
                }).then(() => {
                    this.cancelAddModule();
                });
            }
        }
    }
</script>

<style scoped>
    .card-content-section{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 40px;
    }
</style>